// 
import axios from 'axios';
import { Message, Loading } from 'element-ui';
import conf from './config.js';
import qs from 'qs';

let baseUrl = conf.baseUrl;
let loading;

if (process.env.NODE_ENV === 'development') {
  baseUrl = conf.defaultUrl;
}

function startLoading () { // Element loading-start 
  loading = Loading.service({
    lock: true,
    text: 'loading',
    background: 'rgba(0, 0, 0, 0.2)'
  });
};

function endLoading () { // Element loading-close 
  loading.close();
};

// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'; /*  */
// axios.defaults.withCredentials = true; /*  cookies */
// axios.defaults.timeout = 30000; /*  */

// http request 
axios.interceptors.request.use(config => {
  // 
  if (!(/^(http|https)/.test(config.baseURL))) {
    config.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
    config.baseURL = baseUrl;
    config.withCredentials = true;
    config.timeout = 30000;
  }

  let token = null;
  if (sessionStorage.getItem('token')) {
    token = sessionStorage.getItem('token');
  }
  if (token) {
    switch (config.method) {
      case 'post':
        // form 
        if (config.data && (typeof config.data !== 'string') && !config.data.token) {
          config.data.token = token;
        }
        break;
      case 'get':
        if (config.params && !config.params.token) {
          config.params.token = token;
        }
        break;
    }
  }
  return config;
},
err => {
  return Promise.reject(err);
}
);
// http response 
axios.interceptors.response.use(response => {
  startLoading();
  if (response.data.code && response.data.code === 200) {
    endLoading();
    return response.data;
  } else {
    endLoading();
    // Message.error(response.data.info);
    return Promise.resolve(response.data);
  }
},
error => {
  endLoading();
  Message.error(error.message);
  return Promise.reject(error);
}
);

let postFromData = function (url, data = {}, config) {
  let qsData = '';
  if (data.constructor === Object) {
    qsData = qs.stringify(data);
  }
  return axios.post(url, qsData, config);
};

const getData = function getData (url, data, config) {
  return axios.get(url, data, config);
};

export {
  postFromData,
  getData
};



